<template>
    <div>
        <div class="title">
            <p class="header-line">
                <i class="fa fa-arrow-left m-r-10 pointer" aria-hidden="true" @click="back()"></i>
                {{servicename}}
            </p>
        </div>
        <div class="box-card m-t-20" v-loading="loading">
            <el-form>
                <el-form-item :label="$t('label.name')" :label-width="formLabelWidth">
                    <span class="label-value">{{servicename}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.id')" :label-width="formLabelWidth">
                    <span>{{serviceId}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.createdTime')" :label-width="formLabelWidth">
                    <span>{{createdTime}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.lastUpdateTime')" :label-width="formLabelWidth">
                    <span>{{lastUpdateTime}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.schedulingMode')" :label-width="formLabelWidth">
                    <span>{{schedulingMode}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.image')" :label-width="formLabelWidth" prop="image">
                    <span>{{image}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.replicas')" :label-width="formLabelWidth" >
                    <el-input-number class="w-200" size="small" 
                    v-model="replicas" controls-position="right" 
                    :min="1" :max="10" :disabled="schedulingMode!='Replicated'"></el-input-number>
                </el-form-item>
                <el-form-item  :label-width="formLabelWidth">
                    <el-button type="primary" size="small" 
                    @click="actionContainerSensor('updateServiceReplicas')" :disabled="schedulingMode!='Replicated'">{{$t('global.apply')}}</el-button>
                </el-form-item>
            </el-form>
        </div>
        <p class="header-line m-t-20">
            <i class="fa fa-clone m-r-10 pointer c-primary" aria-hidden="true"></i>
            {{$t('dockerControl.updataImage')}}
        </p>
        <div class="box-card m-t-20 p-t-20" v-loading="loading">
            <el-form>
                <el-form-item :label="$t('label.image')" :label-width="formLabelWidth" prop="image">
                    <el-input v-model.trim="inputImage" autocomplete="off" class="w-500" :placeholder="$t('dockerControl.imageHolder')"></el-input>
                    <el-tooltip class="item" effect="light" :content="$t('dockerControl.imageTooltip')" placement="top-start" >
                        <i class="fa fa-question-circle tooltip" aria-hidden="true"></i>
                    </el-tooltip>
                </el-form-item>
                <el-form-item  :label-width="formLabelWidth">
                    <el-button type="primary" size="small" @click="actionContainerSensor('updateServiceImage')">{{$t('global.apply')}}</el-button>
                </el-form-item>
            </el-form>
        </div>
        <p class="header-line m-t-20">
            <i class="fa fa-tasks m-r-10 pointer c-primary" aria-hidden="true"></i>
            {{$t('dockerControl.tasks')}}
            <i class="fa fa-refresh m-l-10 c-primary pointer" aria-hidden="true" @click="refreshTask()"></i>
        </p>
        
        <div class="box-card m-t-20">
            <el-table
                :data="serviceTaskData"
                tooltip-effect="dark"
                style="width: 100%"
                v-loading="loading">
                <el-table-column
                prop="name"
                :label="$t('dockerControl.name')"
                min-width="120">
                </el-table-column>
                <el-table-column
                prop="state"
                :label="$t('dockerControl.state')"
                min-width="80"
                >
                    <template slot-scope="scope">
                        <div>
                            <span class="badge bg-green" v-if="scope.row.state=='running'">{{scope.row.state}}</span>
                            <span class="badge bg-red"  v-else-if="scope.row.state=='failed'">{{scope.row.state}}</span>
                            <span class="badge bg-aqua"  v-else-if="scope.row.state=='complete'">{{scope.row.state}}</span>
                            <span class="badge bg-SteelBlue"  v-else>{{scope.row.state}}</span>
                        </div>
                    </template>
                </el-table-column>
                
                <el-table-column
                prop="nodeid"
                label="Node"
                min-width="100">
                </el-table-column>
                <el-table-column
                :label="$t('dockerControl.quickAction')"
                min-width="120">
                    <template slot-scope="scope">
                        <div>
                            <el-tooltip content="Log" placement="top">
                            <i class="fa fa-file-text m-r-5 c-primary pointer" aria-hidden="true"
                            @click="getContent('getServiceLog', scope.row)"></i>
                            </el-tooltip>
                            <el-tooltip content="Inspect" placement="top">
                            <i class="fa fa-info-circle m-r-5 c-primary pointer" aria-hidden="true"
                            @click="getContent('getServiceInspect', scope.row)"></i>
                            </el-tooltip>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="updatedat"
                    :label="$t('dockerControl.lastupdate')"
                    min-width="100">
                </el-table-column>
            </el-table>
            <el-dialog  :visible.sync="logDialogVisible" >
                <div slot="title" class="dialog-title">
                    <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                    Log
                </div>
                <div class="text-center" v-loading="logLoading">
                    <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"
                    :options="{
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    tabSize:2,
                    fontSize:14,
                    showPrintMargin:false,   //去除编辑器里的竖线
                    }"></editor>
                </div>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="logDialogVisible = false" size="small">{{$t('global.cancel')}}</el-button>
                </div>
            </el-dialog>
            <el-dialog  :visible.sync="inspectDialogVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-info-circle m-r-10" aria-hidden="true"></i>
                    Inspect
                </div>
                <div  v-loading="logLoading">
                    <!-- <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"></editor> -->
                    <tree-view :data="inspectContent" :options="{maxDepth: 5}"></tree-view>
                </div>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="inspectDialogVisible = false" size="small">{{$t('global.cancel')}}</el-button>
                </div>
            </el-dialog>
        </div>
  
    </div>
</template>
<style lang="scss" scoped>
@import "../../assets/css/colors";
    
    .action{
        background: #fff;
        padding: 5px 20px;
        font-size: 20px;
        color: #1382c6;
        font-weight: bolder;
        text-align: center;
        p{
            margin: 10px 0;
        }
        button{
            width: 150px;
            border-color: #1382c6;
            border-width: 2px;
            color:#1382c6;
            font-weight: bold; 
        }
    }
</style>
<script>
    import {dockerAction} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus} from '@/components/restfulapi/deviceStatusApi';

    export default{
        name: 'containerMonitor',
        data(){
            return {
                formLabelWidth: "150px",
                servicename: "",
                selectedAgentId: "",
                serviceTaskData: [],
                serviceId: "",
                createdTime: "",
                lastUpdateTime: "",
                replicas: 0,
                schedulingMode: "",
                image: "",
                loading: false,
                inputImage: "",
                logLoading: false,
                inspectDialogVisible: false,
                logDialogVisible: false,
                content: "",
                inspectContent: ""
                
            }
        },
        
        methods:{
            getContent(cid, info){
                this.content = "";
                if(cid == "getServiceLog"){
                    this.logDialogVisible = true;
                }else{
                    this.inspectDialogVisible = true;
                }
                let data = {};
                data.id = info.id;
                this.logLoading = true;
                getDeviceStatus(this.selectedAgentId, dockerAction[cid], data).then((obj) => {
                    this.logLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(cid == "getServiceLog"){
                                this.content = res.content.value;
                            }else{
                                this.inspectContent = JSON.parse(res.content.value);
                            }
                        }
                    })   
                })
            },
            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },
            getServiceDetails(serviceId){
                let data = {};
                if(!this.selectedAgentId) {
                    console.error("agentId is empty");
                    return;
                }
                data.id = serviceId;
                this.loading = true;
                getDeviceStatus(this.selectedAgentId, dockerAction["getServiceDetails"], data).then((obj) => {
                    this.loading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            let serviceDetailsData = JSON.parse(res.content.value);
                            this.serviceId = serviceDetailsData.id;
                            this.lastUpdateTime = serviceDetailsData.lastupdate;
                            this.createdTime = serviceDetailsData.createat;
                            this.image = serviceDetailsData.image;
                            let imageArray = serviceDetailsData.image.split("@");
                            this.inputImage = imageArray[0];
                            this.replicas = serviceDetailsData.replicas;
                            this.schedulingMode = serviceDetailsData.schedulingmode;
                            this.serviceTaskData = serviceDetailsData.taskinfos;
                        }
                    })   
                }) 
            },

            refreshTask(){
                this.getServiceDetails(this.serviceId);
            },

            actionContainerSensor(cid){
                if(dockerAction[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }

                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                let obj = {};
                if(cid == "updateServiceReplicas"){
                    obj.serviceId = this.serviceId;
                    obj.replicas = this.replicas;
                }else if(cid == "updateServiceImage"){
                    obj.serviceId = this.serviceId;
                    obj.image = this.inputImage;
                }
                
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerAction."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        setDeviceStatus(this.selectedAgentId, dockerAction[cid], JSON.stringify(obj), true).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getServiceDetails(this.serviceId);                        
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }
                })
            },  

            back(){
                this.$router.go(-1);
            }
        },
        components:{
            editor: require('vue2-ace-editor'),
        },
        created(){
            // console.log(this.$route.query);
            if(this.$route.query.name&& this.$route.query.endpoint){
                this.servicename = this.$route.query.name;
                this.selectedAgentId = this.$route.query.endpoint;
                this.serviceId = this.$route.query.serviceId;
    
                this.getServiceDetails(this.serviceId);
            }
            
        },

        

    }
</script>





